$two: 750px;
$one: 500px;

.grid {
  display: grid;
  --repeat: 3;
  grid-template-columns: repeat(var(--repeat), 1fr);
  justify-content: center;
  align-items: flex-start;
  gap: 40px;
  margin: 40px 0;
}

/* PI区域网格样式（1列布局） */
.grid.pi {
  --repeat: 1; /* 强制1列 */
  justify-items: center; /* 水平居中 */
}

/* 原有样式保持不变 */
.grid > * {
  min-width: 0;
  min-height: 0;
  width: 100%;
  margin: 0 !important;
}

@media (max-width: $two) {
  .grid {
    --repeat: 2;
  }
}

@media (max-width: $one) {
  .grid {
    --repeat: 1;
  }
}

.grid[data-style="square"] {
  align-items: center;

  & > * {
    aspect-ratio: 1 / 1;
  }

  & img {
    aspect-ratio: 1 / 1;
    object-fit: cover;
    max-width: unset;
    max-height: unset;
  }
}

.grid > :where(h1, h2, h3, h4, h5, h6) {
  display: none;
}
